#altrio-description {
  grid-row: 3;
  grid-column: 2 / span 14;
  background-color: #F5F5FB;
  margin-top: -50px;
  padding-top: 100px;
  padding-bottom: 75px;
  display: grid;
  grid-template-columns: repeat(14, 1fr);
  column-gap: 20px;
}

#altrio-description-col-1 {
  grid-row: 1;
  grid-column: 2 / span 4;
}

#altrio-description-col-2 {
  grid-row: 1;
  grid-column: 7 / span 7;
}

#altrio-description p {
  margin-top: 0px;
  margin-bottom: 1.5rem;
}

#values {
  grid-row: 4;
  grid-column: 2 / span 14;
  display: grid;
  grid-template-rows: repeat(2, auto);
  grid-template-columns: repeat(14, 1fr);
  column-gap: 20px;
  color: #0D2443  ;
  margin-bottom: 50px;
}

#values-title {
  grid-row: 1;
  grid-column: 1 / span 14;
  padding: 50px 0px;
}

#value-items {
  grid-column: 1 / span 14;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, auto);
  row-gap: 25px;
  column-gap: 50px;
  padding-bottom: 50px;
}

.value-1 {
  grid-column: 1;
}

.value-2 {
  grid-column: 2;
}

.value-3 {
  grid-column: 3;
}

.value-title {
  grid-row: 2;
}

.value-sub-title {
  grid-row: 3;
  padding-bottom: 25px;
}

.value-sub-title:last-child {
  padding-bottom: 0px;
}


@media only screen and (max-width : 850px) {
  .page-about img#home-hero {
    grid-row: 3;
    grid-column: 2 / span 14;    
  }

  #altrio-description {
    grid-row: 4;
    grid-column: 1 / span 16;
    grid-template-columns: repeat(16, 1fr);
    grid-template-rows: repeat(2, auto);
  }

  #altrio-description-col-1,
  #altrio-description-col-2 {
    grid-column: 3 / span 12;
  }

  #altrio-description-col-2 {
    grid-row: 2;
  }
  
  #values {
    grid-row: 5;
    grid-column: 1 / span 16;
    grid-template-columns: repeat(16, 1fr);   
  }

  #values-title {
    grid-column: 3 / span 12;
  }

  #value-items {
    grid-column: 3 / span 12;
    grid-template-columns: 1fr;
  }

  .value-1, 
  .value-2,
  .value-3 {
    grid-row: unset;
    grid-column: 1;
  }

}