/* Font Family */
.font-sans {
  font-family: 'Work Sans', system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

.font-serif {
  font-family: ui-serif, Cambria, Times, 'Times New Roman', serif;
}

/* Font Size */
.text-xs {
  font-size: .75rem;
}

.text-sm {
  font-size: .875rem;
}

.text-base {
  font-size: 1rem;
}

.text-lg {
  font-size: 1.125rem;
}

.text-xl {
  font-size: 1.25rem;
}

.text-2xl {
  font-size: 1.5rem;
}

.text-3xl {
  font-size: 1.875rem;
}

.text-4xl {
  font-size: 2.25rem;
}

.text-5xl {
  font-size: 3rem;
}

.text-6xl {
  font-size: 4rem;
}

/* Line Height */

.leading-none {
  line-height: 1;
}

.leading-tight {
  line-height: 1.25;
}

.leading-snug {
  line-height: 1.375;
}

.leading-normal {
  line-height: 1.5
}

.leading-relaxed {
  line-height: 1.625;
}

.leading-loose {
  line-height: 2;
}

/* Font Weight */

.font-normal {
  font-weight: 400;
}

.font-medium {
  font-weight: 500;
}

.font-semibold {
  font-weight: 600;
}

.font-bold {
  font-weight: 700;
}

.font-extrabold {
  font-weight: 800;
}

/* Alignment */

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-end {
  text-align: end;
}

.align-self-start {
  align-self: start;
}

/* Blue */

.background-blue {
  background-color: #3A66E3;
}

/* Bright Blue */
.underline-bright-blue {
  text-decoration: underline;
  text-decoration-color: #4973EB;
  text-decoration-line: solid;
}

/* Navy */

.background-navy {
  background-color: #0D2443;
}

/* White */

.text-white {
  color: #fff;
}

.text-blue {
  color: #3A66E3;
}

.text-navy {
  color: #0D2443;
}

/* Padding */

.p-3 {
  padding: 0.75rem;
}

.pt-3 {
  padding-top: 0.75rem
}

.pb-3 {
  padding-bottom: 0.75rem;
}

.px-3 {
  padding-top: 0.75rem;
  padding-bottom:0.75rem;
}

.p-4 {
  padding: 1rem;
}

.pt-4 {
  padding-top: 1rem
}

.pb-4 {
  padding-bottom: 1rem;
}

.px-4 {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.p-5 {
  padding: 1.25rem;
}

.pt-5 {
  padding-top: 1.25rem
}

.pb-5 {
  padding-bottom: 1.25rem;
}

.px-5 {
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
}

.p-6 {
  padding: 1.5rem;
}

.pt-6 {
  padding-top: 1.5rem
}

.pb-6 {
  padding-bottom: 1.5rem;
}

.px-6 {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}

.p-8 {
  padding: 2rem;
}

.pb-8 {
  padding-bottom: 2rem;
}

.pt-8 {
  padding-top: 2rem;
}

.px-8 {
  padding-top: 2rem;
  padding-bottom: 2rem;
}