html {
  color: #0d2443;
  font-family: 'Work Sans';
  font-size: 20px;
  line-height: 1.5;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

#page-container {
  max-width: 1440px;
  margin: auto;
}

#page-container.menu-open {
  position: fixed;
  top: 0px;
  left: calc((100vw - min(100vw, 1440px)) / 2);  
}

#page {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
  column-gap: 20px;
}

.page-home #page-top-bar {
  position: relative;
  grid-row: 1 / span 2;
  grid-column: 1 / span 16;
  background-color: #F5F5FB;
  z-index: 1;
  margin-bottom: 50px;
}

#page-header {
  grid-row: 1;
  grid-column: 2 / span 14;
  padding: 50px 0px;
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  z-index: 4;
}

#logo,
#open-menu,
#close-menu {
  z-index: 4;
}

#logo.hidden,
#alt-logo.hidden {
  display: none;
}

#menu {
  grid-column-start: 3;
  display: grid;
  grid-template-columns: repeat(3, auto);
  column-gap: 50px;
  place-items: center;
}

.menu-item {
  color: #0D2443;
}

.menu-item.active {
  color: #4973EB;
}

#full-screen-menu {
  position: fixed;
  top: 0px;
  right: 0px;
  width: 0px;
  height: 100vh;
  z-index: 3;
  background-color: #3A66E3;  
  padding: 200px 0px;
  transition: all linear 0.25s;
}

#full-screen-menu-items {
  display: grid;
  place-items: center;
}

#full-screen-menu .menu-item {
  display: block;
  margin-bottom: 20px;
  color: white;
  line-height: 48px;
}

#full-screen-menu .btn {
  margin-top: 100px;
}

#open-menu,
#close-menu {
  display: none;
  cursor: pointer;
}

#hero-text {
  grid-row: 2;
  grid-column: 2 / span 6;
  z-index: 2;
  margin-bottom: 50px;
}

#hero-text p {
  margin-block-start: 0;
  margin-block-end: 0.25em;
  display: block;
}

#hero-text > * {
  margin-bottom: 40px;
}

img#home-hero {
  grid-row: 2;
  grid-column: 9 / span 8;
  height: 550px;
  width: 100%;
  object-fit: cover;
  z-index: 2;
}

#introducing-origin {
  grid-row: 3;
  grid-column: 4 / span 10;
  padding: 100px 0px;
}

#highlights {
  grid-row: 4;
  grid-column: 2 / span 14;
  display: grid;
  grid-template-rows: repeat(2, auto);
  grid-template-columns: repeat(14, 1fr);
  column-gap: 20px;
  color: white;
  margin-bottom: 100px;
}

#highlights-title {
  grid-row: 1;
  grid-column: 2 / span 12;
  padding: 50px 0px;
}

#highlight-items {
  grid-row: 2;
  grid-column: 2 / span 12;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, auto);
  row-gap: 25px;
  column-gap: 50px;
  padding-bottom: 50px;
}

.highlight-1 {
  grid-column: 1;
}

.highlight-2 {
  grid-column: 2;
}

.highlight-3 {
  grid-column: 3;
}

#highlight-items img {
  grid-row: 1;
}

.highlight-title {
  grid-row: 2;
}

.highlight-sub-title {
  grid-row: 3;
}

#dots {
  display: none;
}

div.feature {
  grid-column: 2 / span 14;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 20px;
  align-items: center;
  margin-bottom: 100px;
}

div.feature img {
  align-self: center;
  justify-self: center;
  width: 100%;
}

p.feature-title {
  font-weight: 600;
  font-size: 36px;
  line-height: 46px;
}

p.feature-sub-title {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
}

#feature-1 {
  grid-row: 5;
}

#feature-2 {
  grid-row: 6;
}

#feature-3 {
  grid-row: 7;
}

#feature-4 {
  grid-row: 8;
}

div.feature > div, 
div.feature > img {
  grid-row: 1;
}

#feature-1 > img,
#feature-3 > img {
  grid-column: 1;
}

#feature-2 > img,
#feature-4 > img {
  grid-column: 2;
}

#feature-1 > div.feature-text,
#feature-3 > div.feature-text {
  grid-column: 2;
}

#feature-2 > div.feature-text,
#feature-4 > div.feature-text {
  grid-column: 1;
}

#we-are-hiring {  
  grid-row: 9;
  grid-column: 1 / span 16;
  background-color: #CCD5ED;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

#we-are-hiring-image {  
  width: 100%;
  height: 400px;
  object-fit: cover;  
}

#we-are-hiring-text {   
  display: grid;
  place-content: center;
  padding: 0px 100px;
  font-weight: bold;
  font-size: 24px;
}

#we-are-hiring-text p {
  margin-top: 0px;
}

#view-open-positions {
  padding-left: 20px;
  padding-right: 20px;
  width: 250px;
}

#get-started {
  grid-row: 10;
  grid-column: 1 / span 16;  
  display: grid;
  grid-template-columns: repeat(16, 1fr);
  align-items: center;


  background-color: #3A66E3;
  padding: 40px 0px;
}

#get-started-text {
  grid-column: 2 / span 11;
  color: white;
}

#get-started-text p:first-child {
  font-weight: bold;
  font-size: 24px;
  margin-bottom: 5px;
}

#get-started-text p:nth-child(2) {
  font-size: 16px;
  margin-bottom: 5px;
}

#get-started-text p {  
  margin-top: 0px;
}

#get-started .btn {
  grid-column: 13 / span 3;
  justify-self: end;
}

#footer {
  grid-row: 11;
  grid-column: 1 / span 16;
  background-color: #0D2443;
  display: grid;
  grid-template-columns: repeat(16, 1fr);
  column-gap: 20px;
  padding: 30px 0px;
}

#footer-logo {
  grid-column: 2 / span 7;
  align-self: center;
}

#footer-logo path {
  fill: white;
}

#copyright {
  grid-column: 9 / span 7;
}

.btn {
  font-weight: 600;
  font-size: 18px;
  text-align: center;
  height: 50px;
  width: 200px;
  display: grid;
  place-content: center;
  cursor: pointer;
  border: none;
}

.blue {
  color: white;
  background-color: #4973EB;
  transition: all 0.1s ease;
}

.blue:hover {
  background-color: #0D2443;
}

.black {
  color: white;
  background-color: #0D2443;
  transition: all 0.1s ease;
}

.black:hover {
  background-color: #CCD5ED;
  color: #0D2443;
}

.clear {
  background-color: none;
  color: #3A66E3;
  border: solid 2px #3A66E3;
  transition: all 0.1s ease;
}

.clear:hover {  
  background-color: #4973EB;
  color: white;
}

#demo-modal-container.active {
  z-index: 5;
  opacity: 1;
  visibility: visible;
}

#demo-modal-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0,0,0,0.7);  
  visibility: hidden;
  z-index: 4;
}

#demo-modal {
  position: absolute;
  margin: auto;
  max-width: 600px;
  width: 90%;
  height: 400px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 20px 40px 0 rgba(0,0,0,.5);
  background-color: #4973EB;
  color: white;
  display: grid;  
  place-items: center;
  z-index: 5;
}

#close-modal {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
}

#demo-modal form {
  height: 100%;
  display: grid;
  grid-template-rows: auto auto 50px;
  grid-template-columns: 50px 4fr 1fr 50px;
  padding: 50px 0px;
}

#demo-modal p {  
  grid-column: 2 / span 2;  
  margin-top: 0px;  
}

#demo-modal input.email {   
  grid-column: 2;
  font-size: 24px;
  padding-left: 10px;
  border: none;
  outline: none;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  border-radius: none;
}

#demo-modal input.submit {  
  grid-column: 3;  
}

/* ==========================================================================
   Media Queries for Responsive Design.
   ========================================================================== */
@media only screen and (max-width : 1050px) {
  #hero-title {
    font-size: 36px;
  }

  #hero-sub-title {
    font-size: 22px;
  }
}

@media only screen and (max-width : 950px) {
  html {    
    font-size: 18px;    
  }
}

@media only screen and (max-width : 850px) { 
  .page-home #page-top-bar {
    grid-row: 1 / span 3;
  }

  #page-header {
    grid-column: 2 / span 14;
  }

  #menu {    
    column-gap: 0px;
  }

  #menu-request-demo {
    display: none;
  }

  #menu .menu-item {
    visibility: hidden;
  }

  #open-menu {
    display: block;
  }

  #page-container.menu-open #open-menu {
    display: none;
  }

  #page-container.menu-open #close-menu {
    display: block;
  }

  #hero-text {
    grid-row: 2;
    grid-column: 2 / span 14;
  }
  
  img#home-hero {
    grid-row: 3;
    grid-column: 2 / span 14;    
  }

  #introducing-origin {
    grid-row: 4;
    font-size: 28px;
  }

  #highlights {
    grid-row: 5;
    grid-column: 1 / span 16;
    grid-template-columns: repeat(16, 1fr);
  }

  #highlights-title {
    grid-column: 3 / span 12;
    text-align: center;    
  }

  #highlight-items {
    grid-column: 3 / span 12;
    grid-template-columns: 1fr;
  }

  #highlight-items img,
  .highlight-title,
  .highlight-sub-title {
    display: none;
    justify-self: center;
    justify-items: center;    
    text-align: center;
  }

  #highlight-items img.active,
  .highlight-title.active,
  .highlight-sub-title.active {
    display: grid;    
  }

  .highlight-1, 
  .highlight-2,
  .highlight-3 {
      grid-column: 1;
  }
  
  #highlight-items img {
    grid-row: 1;
  }

  #dots {
    grid-row: 3;
    grid-column: 1 / span 16;
    display: flex;
    justify-content: center;
    padding-bottom: 50px;
  }

  .dot {
    display: inline;
    margin-right: 20px;
    cursor: pointer;
  }

  .dot.active circle {
    fill: #4973EB;
  }

  .dot:last-child {
    margin-right: 0px;
  }

  div.feature {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    row-gap: 40px;
    align-items: center;
  }

  #feature-1 {
    grid-row: 6;
  }
  
  #feature-2 {
    grid-row: 7;
  }
  
  #feature-3 {
    grid-row: 8;
  }
  
  #feature-4 {
    grid-row: 9;
  }

  #feature-1 > img,
  #feature-2 > img,
  #feature-3 > img,
  #feature-4 > img {
    grid-column: 1;
    grid-row: 1;
    width: 75%;
  }
  
  #feature-1 > div.feature-text,
  #feature-2 > div.feature-text,
  #feature-3 > div.feature-text,
  #feature-4 > div.feature-text {
    grid-column: 1;
    grid-row: 2;
  }

  #we-are-hiring {
    grid-row: 10;
    grid-template-columns: repeat(16, 1fr);
    grid-template-rows: 1fr auto;
  }

  #we-are-hiring-image {
    grid-row: 1;
    grid-column: 1 / span 16;
  }
  
  #we-are-hiring-text {
    padding: 40px 0px;
    grid-column: 2 / span 14;
    grid-row: 2;
  }

  #get-started {
    grid-row: 11;
    grid-template-rows: auto auto; 
  }

  #get-started-text {
    padding-bottom: 20px;
  }

  #get-started .btn {
    grid-column: 2 / span 14;
    grid-row: 2;
    justify-self: start;
  }

  #footer {
    grid-row: 12;
  }

  #demo-modal {
    width: 100vw;
    height: 100vh;
    max-width: unset;
    top: 0;
    left: 0;
    transform: none;
  }

  #demo-modal form {
    height: auto;
    align-self: center;
    display: grid;
    grid-template-rows: repeat(4, auto);
    grid-template-columns: 1fr 6fr 1fr;
  }
  
  #demo-modal p {
    grid-column: 2;
  }
  
  #demo-modal input.email { 
    grid-row: 3;
    grid-column: 2;
    height: 50px;
    margin-bottom: 25px;
  }
  
  #demo-modal input.submit {
    grid-row: 4;
    grid-column: 2;
    height: 50px;
    width: 200px;
    justify-self: center;
  }

} 

