.page-careers #hero-text {
    margin-bottom: 100px;
}

#benefits {
  grid-row: 4;
  grid-column: 2 / span 14;
  display: grid;
  grid-template-rows: repeat(2, auto);
  grid-template-columns: repeat(14, 1fr);
  column-gap: 20px;
  background-color: #F5F5FB;
  font-family: 'Work Sans';
  font-style: normal;
  font-weight: bold;
  margin-top: -50px;
  padding-top: 100px;
  padding-bottom: 75px;
  margin-bottom: 100px;
}

#benefits-title {
  grid-row: 1;
  grid-column: 2 / span 12;
  font-size: 36px;
  line-height: 46px;
  padding-bottom: 50px;
}

#benefit-items {
  grid-row: 2;
  grid-column: 2 / span 12;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  column-gap: 50px;
}

div.benefit-item {
  display: grid;
  grid-template-rows: 120px auto;
  padding-bottom: 50px;
  cursor: pointer;
}

p.benefit-title {
  font-size: 18px;
}

#positions {
  grid-row: 5;
  grid-column: 2 / span 14;
  display: grid;
  grid-template-columns: 1fr;
  margin-bottom: 50px;
}

#positions-title {
  font-family: 'Work Sans';
  font-weight: bold;
  font-size: 36px;
  line-height: 46px;
  margin-bottom: 50px;
}

.position {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
  padding-bottom: 25px;
  margin-bottom: 25px;
  border-bottom: 2px solid #F5F5FB;
}

.position:last-child {
  border: none;
}

.position-text {
    grid-column: 1 / span 8;
}

.position-text p {
    margin: 0px;
}

.position-text p:first-child {
    font-family: 'Work Sans';
    font-weight: bold;
    font-size: 22px;
}

.position-text p:nth-child(2) {
  font-family: ui-serif;
  font-weight: normal;
  font-size: 16px;
  color: #4973EB;
}

.position a {
    grid-column: 9 / span 4;
    justify-self: end;
}

@media only screen and (max-width : 850px) {
  .page-careers img#home-hero {
        grid-row: 3;
        grid-column: 2 / span 14;    
    }

    #benefits {
        grid-row: 4;
        grid-column: 1 / span 16;
        justify-items: center;
    }

    #benefit-items {
        grid-template-rows: repeat(5, auto);
        grid-template-columns: repeat(16, 1fr);
        column-gap: 20px;
    }

    div.benefit-item {
        grid-column: 3 / span 12;
        text-align: center;
        justify-items: center;
    }

    .position {
        grid-template-rows: auto auto;
        row-gap: 25px;
        padding-bottom: 50px;
        margin-bottom: 50px;
    }

    .position .btn {
        grid-row: 2;
        grid-column: 1 / span 12;
        justify-self: start;
    }
}